* {
  box-sizing: border-box;
  font-family: sans-serif;
}

html, body, #root {
  padding: 0;
  margin: 0;
  height: 100%;
}


.card {
    background: #fff;
    border-radius: 2px;
    position: relative;
    box-shadow: 0 6px 8px rgb(243, 4, 4), 0 3px 6px rgb(2, 43, 224);
    text-align: left;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  
  .card > .img-container {
    overflow: hidden;
    text-align: center;
    background:black;
    
    
  }
  .card > .img-container > img {
    height: 170px;
    width: 200px;
    
  }

  
.wrapper {
    padding-top: 50px;
    background: rgb(48, 67, 240);
    height: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-flow: row wrap;
            flex-flow: row wrap;
    padding: 20px;
    text-align: center;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    -webkit-align-content: flex-start;
            align-content: flex-start;
    overflow: auto;
  }
.title,
.scoreSummary
 {
    width: 100%;
    text-align: center;
    color: black;
    text-shadow: 1px 1px rgb(236, 236, 247);
  }

  .red {
    color: red
  }
.title {
  width: 100%;
  text-align: center;
  margin-bottom: 0px;
}

.scoreSummary{
  width: 100%;
  text-align: center;
  margin-bottom: 20px;	
}

#guess {
float: left;
margin-left: 250px;
}

#score {
  float: right;
  margin-right: 250px;
}
